.wapper {
  width: 100%;
}

.banner {
  width: 100%;
  min-height: 38.0625rem;
}

.marquee {
  align-items: center;
  &_container {
    margin: auto;
    margin-top: 30px;
    display: flex;
    overflow-x: hidden;
  }

  &_gap {
    margin-right: 40px;
  }

  &_item {
    &__content {
      min-width: 203px;
      border-radius: 40px;
      position: relative;
      padding: 12px 36px;
      text-align: center;
      &:before {
        content: "";
        width: 8px;
        height: 14px;
        position: absolute;
        left: 16px;
        background: url("../../../images/0_left@2x.png") left top no-repeat;
        background-size: contain;
      }
      &:after {
        content: "";
        width: 8px;
        height: 14px;
        position: absolute;
        right: 16px;
        background: url("../../../images/0_right@2x.png") left top no-repeat;
        background-size: contain;
      }
    }

    &__suffix {
      margin-left: 30px;
    }
  }
}

