.tokenomics {
  margin-top: 103px;
  padding-top: 100px;

  &_wrapper {
    margin: auto;
    display: flex;
    background-color: #81e3a6;
    border-radius: var(--radiusBold);
  }

  &_left {
    flex: none;
    width: 510px;
    height: 434px;
    text-align: center;
    margin-top: -70px;
    margin-left: -80px;
  }
  &_right {
    flex: 1;
    margin-left: 54px;
    padding-top: 30px;
    padding-bottom: 38px;
    padding-right: 34px;
  }

  &_title {
    border-radius: 30px;
    border: 1px solid #1d1d1d;
    background-color: #fff3a3;
    font-size: 18px;
    max-width: 230px;
    font-weight: 700;
    line-height: 36px;
    text-align: center;
  }
  &_supply {
    color: var(--primaryColor);
    margin-top: 23px;
    line-height: 1;
    &__label {
      font-size: 18px;
    }
    &__count {
      font-size: 60px;
      margin-top: 10px;
    }
  }

  &_list {
    margin-top: 12px;
    margin-bottom: 20px;
    margin-left: 14px;
    list-style: disc;
  }
  &_text {
    font-size: 17px;
    line-height: 28px;
    font-weight: 500;
    color: var(--primaryBoldColor);
  }
}

.address {
  border: 1px solid #1d1d1d;
  border-radius: var(--radius);
  padding: 0 16px;
  height: 38px;
  line-height: 38px;
  justify-content: space-between;

  &_lable {
    font-size: 22px;
    line-height: 30px;
  }

  &_value {
    font-size: 14px;
  }

  &_copy {
    margin-left: 10px;
    width: 12px;
  }
}
