.roadmap {

  &_title {
    padding: 42px 0;
    width: 48vw;
    max-width: 100%;
    margin: 166px auto 0;
  }
}

.carousel {
  margin: 56px auto 0;
  display: flex;
  justify-content: center;
  &_item {
    flex: none;
    width: 284px;
    border-radius: var(--radius);
    &+& {
      margin-left: 14px;
    }
  }

  &_content {
    height: 100%;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    border-radius: var(--radiusBold);
    padding: 12px;
  }

  &_step {
    top: -21px;
    position: absolute;
    width: 100px;
    height: 40px;
    border: 1px solid var(--primaryBoldColor);
    border-radius: 30px;
    color: rgb(40 40 40);
    background-color: rgb(244 244 244);
    text-align: center;
    line-height: 40px;
    font-size: 19px;
    font-weight: 400;
  }

  &_text {
    margin-top: 20px;
    margin-bottom: 20px;
    text-align: center;
    font-size: 24px;
    line-height: 1.2;
    color: var(--primaryBoldColor);
  }

  // img {
  //   max-width: 80%;
  // }

}