.thanks {
  display: flex;
  margin-top: 76px;
  background: #81E3A6;

  &_wrapper {
    max-width: 1080px;
    width: 69vw;
    padding: 33px 0;
    margin: auto;
    display: flex;
    align-items: center;
  }

  &_left {
    flex: none;
    width: 15.3vw;
    max-width: 292px;
  }
  &_right {
    margin-left: 40px;
    color: var(--primaryColor);
  }

  &_title {
    font-size: 44px;
    line-height: 44px;
  }

  &_text {
    margin-top: 20px;
    font-size: 18px;
    line-height: 24px;
  }
}