.about {
  width: 76vw;
  max-width: 980px;
  padding-top: 60px;
  margin: auto;
  display: flex;
  align-items: center;

  &_box {
    flex: none;
    width: 284px;
    height: 284px;
    padding: 34px 20px;
    border-radius: var(--radius);
    background: #B7CCAF;
    text-align: center;
  }

  &_desc {
    margin-left: 47px;
    color: var(--primaryColor);
    &__title {
      font-size: 44px;
      line-height: 44px;
    }

    &__text {
      margin-top: 20px;
      font-size: 16px;
      line-height: 22px;
    }
  }
}